import React, { PureComponent } from 'react';
import {Link} from 'react-router-dom';

export default class ListActsForEvery extends PureComponent {
	render() {
		return (
			<div className = "col-md-1 d-print-none">
				{this.props.cntxt.state.acts.filter((x) => x.isforevery).map((itm)=>
					{switch (itm.type) {
						case "Link": return (
							<Link 
								className = {itm.classname || "btn"} key = {itm.title} title = {itm.title} 
								style={{ textDecoration: 'none', color:"black" }} 
								to = {itm.act +  this.props.cntxt.queryBuilder(itm, this.props.item) }>
								{(!itm.icon || itm.icon.length===0)?
									<label>{itm.title}</label>
								:
									<i className={itm.icon || itm.classname}></i>
								}												
							</Link>
						)	
						case "API": return (
							<button 
								title = {itm.title} className = {itm.classname || "btn btn"} key = {itm.title} 
								onClick = {() => this.props.cntxt.callApi(itm.act +  this.props.cntxt.queryBuilder(itm, this.props.item), itm)}>
								{(!itm.icon || itm.icon.length===0)?
									<i>{itm.title}</i>
								:
									<i className={itm.icon || itm.classname}></i>
								}
							</button>
						)
						case "Delete": return (
							<button title = {itm.title} className = {itm.classname || "btn btn"} key = {itm.title} onClick = {() => this.props.cntxt.delRow(this.props.item[this.props.cntxt.state.config.filter((obj)=> obj.col.toLowerCase() === 'id' && !obj.relation)[0].title], itm.roles)}>
								{(!itm.icon || itm.icon.length===0)?
									<i>{itm.title}</i>
								:
									<i className={itm.icon || itm.classname}></i>
								}
							</button>
						)
						default:
							return <div />
					}}
				)}
																														
			</div>

		)			
	}
}