import React, { PureComponent } from 'react';
import {Link} from 'react-router-dom';

export default class ListActs extends PureComponent {
	render() {
		return (
			<div className = "col-md-10 row d-print-none">				
				{this.props.cntxt.state.acts.filter((x) => !x.isforevery).map((itm)=>					
					{switch (itm.type) {
						case "Link": return (
							<Link key = {itm.title} title = {itm.title}
								className = {itm.classname || "btn"}
								style={{ textDecoration: 'none', color:"black" }} 
								to = {itm.act + this.props.cntxt.queryBuilder2(itm)} >
								{(!itm.icon || itm.icon.length===0)?
									<i>{itm.title}</i>
								:
									<i className={itm.icon || itm.classname}></i>
								}
							</Link>
						)
						case "Delete": return (
							<button className = {itm.classname || "btn"} key = {itm.title} >	
								{(!itm.icon || itm.icon.length===0)?
									<i>{itm.title}</i>
								:
									<i className={itm.icon || itm.classname}></i>
								}
							</button>
						)
						case "API": return (
							<button title = {itm.title} className = {itm.classname || "btn btn"} key = {itm.title} 
								onClick = {() => this.props.cntxt.callApi(itm.act +  this.props.cntxt.queryBuilder(itm,this.props.cntxt.state.inputs),  itm)}>
								{(!itm.icon || itm.icon.length===0)?
									<i>{itm.title}</i>
								:
									<i className={itm.icon || itm.classname}></i>
								}
							</button>
						)
						case "Print Data": return (
							<Link key = {itm.title} title = {itm.title}
								className = {itm.classname || "btn"}
								style={{ textDecoration: 'none', color:"black" }} 
								to = {'/printlist/' + this.props.cntxt.state.path + this.props.cntxt.printQueryBuilder()  } >
								{(!itm.icon || itm.icon.length===0)?
									<i>{itm.title}</i>
								:
									<i className={itm.icon || itm.classname}></i>
								}
							</Link>
						)							
						default:
							return <div />
					}})
				}		
			</div>
		)
	}
}