import React, { PureComponent } from 'react';
import Select from 'react-select';


export default class ListFilters extends PureComponent {

	render() {
		return (
			<div className = "d-print-none">
				<button className="card-header btn btn-link" 
				   onClick = {() => {
					if (this.props.cntxt.state.fltvis === 'none') 
						this.props.cntxt.setState({fltvis:'block', fltbtitle:'-'})
					else 
						this.props.cntxt.setState({fltvis:'none', fltbtitle:'+'})	
				   }}>
				   filters {this.props.cntxt.state.fltbtitle}
				 </button>
				 <div 
					className = "card" 
					style = {{display:this.props.cntxt.state.fltvis, background:"#ffffefd9"}}>
					{this.props.cntxt.state.filters.map((item) => (
						<div key = {JSON.stringify(item)} >
							{(() => {	switch(item.type) {
								case "substr":
									return (
										<div key = {item.title}>
											<label>{item.title}  :</label>
											<input  placeholder = {item.title}
													className = {item.classname || 'form-control'}
													onChange = {(e) => this.props.cntxt.onFilterChange(item.column, e.target.value)} 	/>
										</div>
									)
								case "select":
									return (
										<div key = {item.title}>
											<label>{item.title}  :</label>
											<Select
												className = {item.classname}
												onChange = {(e) => this.props.cntxt.onFilterChange(item.column, e.value)}
												options = {this.props.cntxt.state.kfilters[item.column] || []}
												onFocus = {()=>this.props.cntxt.getTable(item.column,item.table)} />
										</div>
									)	
								case "multiselect":
									return (
										<div key = {item.title}>
											<label>{item.title}  :</label>
											<Select
												className = {item.classname}
												isMulti
												onChange = {(e) => this.props.cntxt.onFilterChange(item.column, e)}
												options = {this.props.cntxt.state.kfilters[item.column] || []}
												onFocus = {()=>this.props.cntxt.getTable(item.column,item.table)} />
										</div>
									)		
								case "multijson":
									return (
										<div key = {item.title}>
											<label>{item.title}  :</label>
											<Select
												className = {item.classname}
												isMulti
												onChange = {(e) => this.props.cntxt.onFilterChange(item.column, e)}
												options = {this.props.cntxt.state.kfilters[item.column] || []}
												onFocus = {()=>this.props.cntxt.getTable(item.column,item.multiselecttable)} />
										</div>
									)										
								case "typehead":
									return (
										<div key = {item.title}>
											<label>{item.title}  :</label>
											<input placeholder = {item.title}
												className = {item.classname || 'form-control'}	
												onChange = {(e) => this.props.cntxt.setState({th:e.target.value})}
												onKeyPress = {(e) => {
													if (e.key === 'Enter') {
														if (e.target.value.length>=3 || e.target.value.length === 0) 
															this.props.cntxt.onFilterChange(item.title, this.props.cntxt.state.th) 								
														
														if (e.target.value.length<=3) {
															let p = this.props.cntxt.state.paramfilters
															p[item.title] = e.target.value
															this.props.cntxt.setState({paramfilters:p, th:e.target.value})
														}
													}
												}} 
												value = {this.props.cntxt.state.th}
											/>
										</div>
									)			
								case "check":
									return (
										<div key = {item.title}>
											<label>{item.title}  :</label>
											<input placeholder = {item.title}
												className = {item.classname || 'form-control'}
												type = "checkbox"
												onChange = {(e) => this.props.cntxt.onFilterChange(item.column, e.target.checked)} 	/>
										</div>
									)			
								case "period":
									return (
										<div key = {item.title}>
											<label>{item.title}  :</label>
											<div className = "row" style = {{marginLeft:'1px'}}>
												<input 
													placeholder = {item.title}
													style = {{width:"150pt"}}
													className = {item.classname || 'form-control'}
													type = "date"										
													onChange = {(e) => this.props.cntxt.onFilterChangeDate(item.column, e.target.value, 'date1')} 	/>
												<label> - </label>	
												<input 
													placeholder = {item.title}
													style = {{width:"150pt"}}
													className = {item.classname || 'form-control'}
													type = "date"										
													onChange = {(e) => this.props.cntxt.onFilterChangeDate(item.column, e.target.value,'date2')} 	/>	
											</div>
										</div>
									)		
								default:
									return (<div />)
							}})()
						}	
					</div>))}	
				</div>
			</div>
		)
	}
}