	export function  queryBuilder2(itm) {
		let squery = ''
		if (itm.act.indexOf('?') === -1)
			squery = '?'
		if (itm.parametrs) {
			itm.parametrs.forEach((obj) => {
				if (obj.paramcolumn) {
					squery += obj.paramtitle + "=" + 
						(this.state.data[this.state.config.filter((x)=> x.col === obj.paramcolumn.label)[0].title] 
							|| 
						 this.state.inputs[this.state.config.filter((x)=> x.col === obj.paramcolumn.label)[0].title]) + '&'
				}
				else {
					squery += obj.paramtitle + "=" + obj.paramconst + '&'
				}
			}) 
		}
		
		squery = squery.substring(0,squery.length-1)
		return squery
	}	
	
	export function printQueryBuilder() {
		let s = this.state.query || ''
		if (s.length === 0) s = '?'
		else s += '&'
		if (this.state.paramfilters) 
			Object.keys(this.state.paramfilters).forEach((x) => s += x + '=' + this.state.paramfilters[x] + '&')
		return s		
	}
	
	export function ifParse(val) {
		if (val) {
			return val
		} else {
			return []
		}
	} 
	
	export function format(val) {
		if (val) {
			val = val.toString()
		}
		return val
	}
	
	export function callApi(uri, itm) {
		this.MDLP_API = u_methods.MDLP_API
		if (!itm.actapimethod || itm.actapimethod === 'simple')
			this.setState({loading:true},() => {
				apishka(itm.actapitype, {}, {}, uri, 
					(data) => {
						this.setState({loading:false}, () => {
							NotificationManager.success("OK")
						})
					}, (err) => {
						this.setState({loading:false}, () => {
							if (err.response && err.response.data) {
								NotificationManager.error(err.response.data.message)
							}
							else {
								NotificationManager.error('unknown error!')
							}	
						})
					}
				)
			})	
		else if (!itm.actapimethod || itm.actapimethod === 'mdlp')	
			this.MDLP_API(uri, itm)
	}